import React from 'react';
import personService from '../services/persons';

const PersonForm = ({ persons, setPersons, newName, setNewName,
  newNumber, setNewNumber, handleNameChange, handleNumberChange,
  setNotificationMessage }) => {

  const addPerson = (event) => {
    event.preventDefault();

    if (newName.length === 0 || newNumber.length === 0) {
      window.alert("Name and number must be non-empty");
      return;
    }

    if (persons.find(element => element.name === newName.trim())) {
      window.alert(`${newName.trim()} is already added to the phonebook`);
      return;
    }

    const personObject = {
      name: newName.trim(),
      number: newNumber.trim()
    }

    personService
      .create(personObject)
      .then(returnedPerson => {
        setPersons(persons.concat(returnedPerson));
        setNewName('');
        setNewNumber('');
        setNotificationMessage(`Added ${personObject.name}`);
      });

    setTimeout(() => { setNotificationMessage(null) }, 5000)
  }

  return (
    <form onSubmit={addPerson}>
      <div>
        Name: <input value={newName} onChange={handleNameChange} />
      </div>
      <div>
        Number: <input value={newNumber} onChange={handleNumberChange} />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  )
}

export default PersonForm