import React from 'react';
import personService from '../services/persons'

const Button = ({ handleClick, text }) =>
  <button onClick={handleClick}>{text}</button>

const Listing = ({ name, number, handleClick }) => {
  return (
    <div>
      {name} {number} <Button handleClick={handleClick} text="Delete" />
    </div>
  );
}

const PersonsToShow = ({ persons, setPersons, newSearch }) => {
  const filtered = persons.filter(person =>
    person.name.toLocaleLowerCase().includes(newSearch.toLocaleLowerCase()));

  // An event handler is supposed to be either a function or a function reference
  const handleDeleteClick = (id) => () => {
    personService
      .personDelete(id)
      .then(() => {
        setPersons(persons.filter(person => person.id !== id))
      })
  }
  return (
    <div>
      {
        filtered.map(person =>
          <Listing key={person.id} name={person.name} number={person.number}
            handleClick={handleDeleteClick(person.id)} />
          // handleClick={
          //   () => {
          //   personService.personDelete(person.id);
          //   setPersons(persons.filter(p => p.id !== person.id))
          // }} />
        )
      }
    </div>
  );
}

export default PersonsToShow;